import { useState, useLayoutEffect, useMemo, useCallback } from "react";
import {
  Box,
  Grid,
  Stack,
  Typography,
  TextField,
  MenuItem,
} from "@mui/material";
import Select, { SelectChangeEvent } from "@mui/material/Select";
import { DataGrid, GridColDef } from "@mui/x-data-grid";
import { restApi } from "../../providers/restapi";
// import { useBlockchainContext } from "../../providers";

const columns: GridColDef[] = [
  {
    field: "image",
    headerName: "Image",
    headerAlign: "center",
    align: "center",
    sortable: false,
    minWidth: 130,
    renderCell: (params) => (
      <Box
        component="img"
        src={params.value}
        alt=""
        sx={{
          width: "50px",
          height: "50px",
          objectFit: "cover",
        }}
      ></Box>
    ),
  },
  {
    field: "name",
    headerName: "Name",
    headerAlign: "center",
    align: "center",
    minWidth: 150,
    flex: 1 / 2,
  },
  {
    field: "address",
    headerName: "Address",
    headerAlign: "center",
    align: "center",
    minWidth: 150,
    flex: 1,
  },
  {
    field: "merit",
    headerName: "Merit",
    headerAlign: "center",
    align: "center",
    type: "number",
    minWidth: 120,
    flex: 1,
  },
  {
    field: "rank",
    headerName: "Ranking",
    headerAlign: "center",
    align: "center",
    type: "number",
    minWidth: 110,
    flex: 1 / 4,
  },
];

const rewardRate = [
  "3000",
  "2700",
  "2400",
  "2100",
  "1800",
  "1500",
  "1200",
  "900",
  "600",
  "300",
];

const image1 = "https://images.unsplash.com/photo-1551963831-b3b1ca40c98e";

export default function RewardsPage() {
  const [filter, setFilter] = useState("");
  const [allData, setAllData] = useState([]);

  useLayoutEffect(() => {
    (async () => {
      const result = await restApi.getAllProfiles();
      console.log("result", result);
      setAllData(result);
    })();
  }, []);

  const RowsFilter = useCallback(
    (item: any) => {
      const searchParams = ["name", "address"];
      return searchParams.some((newItem) => {
        try {
          return (
            item[newItem]
              .toLowerCase()
              .indexOf(filter.toLowerCase().trim()) > -1
          );
        } catch (err) {
          return false;
        }
      });
    },
    [filter]
  );

  const rows = useMemo(() => {
    return allData
      .sort((a: any, b: any) => {
        return a.merit < b.merit ? 1 : -1;
      })
      .map((item: any, index: number) => {
        return { ...item, rank: index + 1 };
      })
      .filter(RowsFilter);
  }, [allData, filter]);

  const tenRows = useMemo(() => {
    const bump = allData
      .sort((a: any, b: any) => {
        return a.merit < b.merit ? 1 : -1;
      })
      .map((item: any, index: number) => {
        return { ...item, rank: index + 1 };
      })
      .filter((item: any) => {
        if (item.merit > 0) return true;
        return false;
      });
    return bump.splice(0, 10);
  }, [rows]);

  return (
    <>
      <Box
        sx={{
          backgroundColor: "secondary.dark",
          border: "1px solid #c76e36",
          borderRadius: "20px",
          padding: "10px",
        }}
      >
        <Stack
          direction="row"
          justifyContent="flex-start"
          alignItems="center"
          sx={{
            p: 1,
            gap: 1,
          }}
        >
          <Box
            component="img"
            src={image1}
            alt=""
            sx={{
              textAlign: "center",
              width: "60px",
            }}
          />
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              alignItems: "left",
              justifyContent: "center",
            }}
          >
            <Typography>{"Rewards"}</Typography>
            <Typography style={{ color: "goldenrod" }}>
              {"10000 DFTL"}
            </Typography>
          </Box>
        </Stack>
        <hr />
        <Grid container spacing={2} padding={2}>
          {tenRows.map((item: any, index: number) => (
            <Grid item xs={12} md={4} lg={3} key={index}>
              <RoundItem
                image={item.image}
                tankOwner={item.name + "'s tank"}
                exp={item.merit}
                amount={rewardRate[index]}
              />
            </Grid>
          ))}
        </Grid>
        <Grid container spacing={2} sx={{ p: 1 }}>
          <Grid item xs={12}>
            <TextField
              placeholder="search..."
              sx={{ width: "100%" }}
              value={filter}
              onChange={(e: any) => setFilter(e.target.value)}
            />
          </Grid>
        </Grid>
        <br />
        <Box sx={{ height: 812, width: "100%" }}>
          <DataGrid
            rows={rows}
            columns={columns}
            pageSize={10}
            getRowId={(row) => row.rank}
            rowsPerPageOptions={[10]}
            rowHeight={70}
          />
        </Box>
      </Box>
    </>
  );
}

const RoundItem = (props: any) => {
  const { image, tankOwner, exp, amount } = props;

  return (
    <Stack
      direction="row"
      justifyContent="space-between"
      alignItems="center"
      sx={{
        background:
          "linear-gradient(to right, #c76e36 30%,  #c76e3650)",
        p: 1,
        borderRadius: 2,
      }}
    >
      <Stack
        direction="row"
        justifyContent="flex-start"
        alignItems="center"
      >
        <Box
          component="img"
          src={image}
          alt=""
          sx={{
            textAlign: "center",
            width: "70px",
            height: "70px",
            pr: 2,
          }}
        />
        <Box
          sx={{
            display: "flex",
            flexDirection: "column",
            alignItems: "left",
            justifyContent: "center",
          }}
        >
          <Typography>{tankOwner}</Typography>
          <Typography>{exp}</Typography>
        </Box>
      </Stack>
      <Typography sx={{ color: "yellow", fontSize: "18px" }}>
        {amount} DFTL
      </Typography>
    </Stack>
  );
};
