import { useEffect } from "react";
import {
  Box,
  Stack,
} from "@mui/material";
import { Unity, useUnityContext } from "react-unity-webgl";

import { ActionButton2 } from "../../components/buttons";

const GamePage = () => {

  const { unityProvider, requestFullscreen, unload, isLoaded } = useUnityContext({
    loaderUrl: "build/Build/build.loader.js",
    dataUrl: "build/Build/build.data",
    frameworkUrl: "build/Build/build.framework.js",
    codeUrl: "build/Build/build.wasm",
  });

  function handleClickEnterFullscreen() {
    requestFullscreen(true);
  }

  useEffect(() => {
    return () => { if (isLoaded) unload() }
  }, [isLoaded, unload])

  const handleGoOtherPage = async () => {
    console.log("unload");
    await unload();
  }

  return (
    <Box sx={style}>
      <Stack alignItems={"center"} spacing={2}>
        <Unity
          unityProvider={unityProvider}
          style={{ border: "1px solid red", width: "100%" }}
        // devicePixelRatio={window.devicePixelRatio}
        />
        <ActionButton2 onClick={handleClickEnterFullscreen}>Enter Fullscreen</ActionButton2>
        {/* <ActionButton2 onClick={handleGoOtherPage}>Go to page</ActionButton2> */}
      </Stack>
    </Box>
  );
};

const style = {
  width: "100%",
  height: "100%"
}
export default GamePage;
