import { useState, useMemo, useCallback } from "react";
import {
  Box,
  Stack,
  TextField,
  FormControl,
  InputLabel,
  MenuItem,
  Grid,
  Typography
} from "@mui/material";

import Select from "@mui/material/Select";
import InfiniteScroll from "react-infinite-scroll-component";
import { useBlockchainContext } from "../../providers";

import { TankItemCard } from "../../components/tankCard";

const LendingPage = () => {
  const [state] = useBlockchainContext();
  const [sort, setSort] = useState("");
  const [filter, setFilter] = useState("");
  const [renderCount, setRendCount] = useState(10);
  const [hasMore, setHasMore] = useState(true);
  const fetchMore = () => {
    setRendCount(renderCount + 10);
  }

  const sortBy = useCallback(
    (a: TankItem, b: TankItem) => {
      var res: boolean = true;
      switch (sort) {
        case "energyLH":
          res = Number(a.energy) > Number(b.energy);
          break;
        case "energyHL":
          res = Number(a.energy) < Number(b.energy);
          break;
        case "speedLH":
          res = Number(a.speed) > Number(b.speed);
          break;
        case "speedHL":
          res = Number(a.speed) < Number(b.speed);
          break;
        case "healthLH":
          res = Number(a.health) > Number(b.health);
          break;
        case "healthHL":
          res = Number(a.health) < Number(b.health);
          break;
        case "fireRateLH":
          res = Number(a.fireRate) > Number(b.fireRate);
          break;
        case "fireRateHL":
          res = Number(a.fireRate) < Number(b.fireRate);
          break;
        case "firePowerLH":
          res = Number(a.firePower) > Number(b.firePower);
          break;
        case "firePowerHL":
          res = Number(a.firePower) < Number(b.firePower);
          break;
      }
      return res ? 1 : -1;
    },
    [sort]
  );

  const myTanks = useMemo(() => {
    let items = state.tankItems
      .filter((tankItem: TankItem) => {
        return tankItem.borrower === "";
      })
      .filter((item: TankItem) => {
        return item.name?.toLowerCase().indexOf(filter.toLowerCase().trim()) > -1;
      })
      .sort(sortBy);
    let renderItems = items.slice(0, renderCount);
    if (renderItems.length === items.length) setHasMore(false);
    else setHasMore(true);
    return renderItems
  }, [state.account, state.tankItems, filter, sort]);

  return (
    <Box>
      <Stack
        marginTop={"30px"}
        spacing={2}
        direction={{ xs: "column", sm: "row" }}
        justifyContent="space-between"
        alignItems="center"
      >
        <TextField
          id="Search-basic"
          label="Search"
          variant="outlined"
          sx={{ width: { xs: "100%" }, maxWidth: "600px", flex: 1 }}
          value={filter}
          onChange={(e: any) => setFilter(e.target.value)}
        />
        <FormControl
          sx={{ width: { xs: "100%" }, maxWidth: "400px", flex: 1 }}
          size="medium"
        >
          <InputLabel id="sort-select">Sort</InputLabel>
          <Select
            labelId="sort-select"
            id="sort-select"
            value={sort}
            label="Sort"
            onChange={(e: any) => setSort(e.target.value)}
          >
            <MenuItem value="">
              <em>None</em>
            </MenuItem>
            <MenuItem value={"energyHL"}>Energy(High-Low)</MenuItem>
            <MenuItem value={"energyLH"}>Energy(Low-High)</MenuItem>
            <MenuItem value={"speedHL"}>Speed(High-Low)</MenuItem>
            <MenuItem value={"speedLH"}>Speed(Low-High)</MenuItem>
            <MenuItem value={"healthHL"}>Health(High-Low)</MenuItem>
            <MenuItem value={"healthLH"}>Health(Low-High)</MenuItem>
            <MenuItem value={"fireRateHL"}>
              FireRate(High-Low)
            </MenuItem>
            <MenuItem value={"fireRateLH"}>
              FireRate(Low-High)
            </MenuItem>
            <MenuItem value={"firePowerHL"}>
              FirePower(High-Low)
            </MenuItem>
            <MenuItem value={"firePowerLH"}>
              FirePower(Low-High)
            </MenuItem>
          </Select>
        </FormControl>
      </Stack>
      <InfiniteScroll
        dataLength={myTanks.length}
        next={fetchMore}
        hasMore={hasMore}
        loader={<Typography>Loading...</Typography>}
      >
        <Grid container spacing={2} sx={{ mt: 1 }}>
          {myTanks.map((tankClass: TankItem, index: any) => (
            <Grid
              key={index}
              item
              xs={12}
              sm={6}
              md={6}
              lg={4}
              xl={2.4}
            >
              <TankItemCard item={tankClass} />
            </Grid>
          ))}
        </Grid>
      </InfiniteScroll>
    </Box>
  );
};

export default LendingPage;
