import { styled } from "@mui/material/styles";
import {
  Box,
  TextField,
  Stack,
  Paper,
  Button,
  IconButton,
} from "@mui/material";
import { useState } from "react";
import { ActionButton1, ActionButton2 } from "../../../components/buttons";
import { restApi } from "../../../providers/restapi";
import { useBlockchainContext } from "../../../providers";
import { ethers } from "ethers";
import { AddNotification } from "../../../utils/views";

export const EditPanel = ({ item, onClose }: { item: TankItem, onClose: CallableFunction }) => {
  const [state] = useBlockchainContext();
  const [name, setName] = useState(item.name);
  const [description, setDescription] = useState(item.description);

  const submit = async () => {
    try {
      let signature: string = await state.signer.signMessage(item.id);
      await restApi.updateName(item.id, name, description, signature);
      AddNotification("Success", `Tank${item.id} name set to ${name}`, "success");
      onClose()
    } catch (err: any) {
      console.log("handle lend", err.message);
      AddNotification("Failed", err.message, "danger");
    }
  }

  return (
    <Box sx={style}>
      <Stack spacing={2}>
        <TextField label="name" value={name} onChange={(e) => { setName(e.target.value) }} />
        <TextField label="description" value={description} onChange={(e) => { setDescription(e.target.value) }} />
        <Stack direction="row" spacing={2} justifyContent="center">
          <ActionButton1 onClick={submit}>Submit</ActionButton1>
        </Stack>
      </Stack>
    </Box >
  )
}

const style = {
  position: "absolute" as "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: { xs: "230px", sm: "300px", md: "600px" },
  flex: 1,
  bgcolor: "background.paper",
  border: "2px solid background.paper",
  boxShadow: "0 3px 10px #000",
  p: 4,
  borderRadius: "5px",
};