import {
  Stack,
  Card,
  CardHeader,
  CardMedia,
  CardContent,
  CardActions,
  IconButton,
  Typography,
} from "@mui/material";
import { useNavigate } from "react-router-dom";
import FavoriteIcon from "@mui/icons-material/Favorite";

import { ActionButton1 } from "../../../components/buttons";
import { ItemCard } from "../../../components/cards";
import { useBlockchainContext } from "../../../providers";
import { AddNotification } from "../../../utils/views";
export const Item = ({ item }: { item: TankClass }) => {
  const [state, { mintNFT }] = useBlockchainContext();
  /* ----- actions ----- */
  const navigate = useNavigate();

  const handleDetail = () => {
    navigate("/class-detail/" + item.id);
  };

  const handleBuy = async () => {
    try {
      var tx = await mintNFT(item.id, item.price);
      await tx.wait();
      AddNotification("Buy Success", tx.hash, "success");
    } catch (err: any) {
      console.log("error", err.code);
      if (err.code === "ACTION_REJECTED")
        AddNotification("Buy Failed", "User Denied ACTION", "danger");
      else if (err.code === "UNPREDICTABLE_GAS_LIMIT")
        AddNotification("Buy Failed", "Please check DFTL Balance", "danger");
      else
        AddNotification("Buy Failed", err.message, "danger");
    }
  };

  return (
    <ItemCard>
      <CardMedia
        component="img"
        height="194"
        image={item.image}
        alt="image"
        onClick={handleDetail}
      />
      <CardContent>
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
        >
          <Typography>{item.name}</Typography>
          <Typography>{item.price + " DFTL"}</Typography>
        </Stack>
      </CardContent>

      <CardActions
        sx={{ display: "flex", justifyContent: "space-around" }}
      >
        <ActionButton1 variant="outlined" onClick={handleBuy}>
          BUY
        </ActionButton1>
        <ActionButton1 variant="outlined" onClick={handleDetail}>
          Detail
        </ActionButton1>
      </CardActions>
    </ItemCard>
  );
};
