import { useState, useMemo, SyntheticEvent, useCallback } from "react";
import {
  Box,
  Stack,
  TextField,
  FormControl,
  InputLabel,
  MenuItem,
  Grid,
  Typography,
} from "@mui/material";
import { useParams } from "react-router-dom";
import Select, { SelectChangeEvent } from "@mui/material/Select";
import InfiniteScroll from "react-infinite-scroll-component";

import { useBlockchainContext } from "../../providers";
import PoolItem from "./components";
import { GridItem } from "../../components/layout";

const PoolsPage = () => {
  const [state] = useBlockchainContext();
  const [sort, setSort] = useState("capacityHL");
  const [filter, setFilter] = useState("");
  const [renderCount, setRendCount] = useState(20);
  const [hasMore, setHasMore] = useState(true);

  const fetchMore = () => {
    setRendCount(renderCount + 15);
  }

  const sortBy = useCallback(
    (a: TankItem, b: TankItem) => {
      var res: boolean = true;
      switch (sort) {
        case "poolSizeHL":
          res = Number(a.maxEnergyPool) < Number(b.maxEnergyPool);
          break;
        case "poolSizeLH":
          res = Number(a.maxEnergyPool) > Number(b.maxEnergyPool);
          break;
        case "capacityHL":
          res = Number(a.maxEnergyPool - a.energyPool) < Number(b.maxEnergyPool - b.energyPool);
          break;
        case "capacityLH":
          res = Number(a.maxEnergyPool - a.energyPool) > Number(b.maxEnergyPool - b.energyPool);
          break;
        case "stakedHL":
          res = Number(state.stakes[a.id]) < Number(state.stakes[b.id]);
          break;
        case "stakedLH":
          res = Number(state.stakes[a.id]) > Number(state.stakes[b.id]);
          break;
      }
      return res ? 1 : -1;
    },
    [sort]
  );

  const tankItems = useMemo(() => {
    let items = state.tankItems
      .filter((item: TankItem) => {
        return item.name?.toLowerCase().indexOf(filter.toLowerCase().trim()) > -1;
      })
      .sort(sortBy);
    let renderingItems = items.slice(0, renderCount);
    if (renderingItems.length === items.length) setHasMore(false)
    else setHasMore(true);

    return renderingItems
  }, [state.tankItems, filter, sort]);

  const [expanded, setExpanded] = useState<string | false>(false);
  const handleExpand =
    (panel: string) => (event: SyntheticEvent, isExpanded: boolean) => {
      setExpanded(isExpanded ? panel : false);
    };


  return (
    <Box>
      <Stack
        marginTop={"30px"}
        spacing={2}
        direction={{ xs: "column", sm: "row" }}
        justifyContent="space-between"
        alignItems="center"
      >
        <TextField
          id="Search-basic"
          label="Search"
          variant="outlined"
          sx={{ width: { xs: "100%" }, maxWidth: "600px", flex: 1 }}
          value={filter}
          onChange={(e: any) => { setFilter(e.target.value); setRendCount(20) }}
        />
        <FormControl
          sx={{ width: { xs: "100%" }, maxWidth: "400px", flex: 1 }}
          size="medium"
        >
          <InputLabel id="sort-select">Sort</InputLabel>
          <Select
            labelId="sort-select"
            id="sort-select"
            value={sort}
            label="Sort"
            onChange={(e: any) => setSort(e.target.value)}
          >
            <MenuItem value="">
              <em>None</em>
            </MenuItem>
            <MenuItem value={"capacityHL"}>Capacity(High-Low)</MenuItem>
            <MenuItem value={"capacityLH"}>Capacity(Low-High)</MenuItem>
            <MenuItem value={"stakedHL"}>Staked(High-Low)</MenuItem>
            <MenuItem value={"stakedLH"}>Staked(Low-High)</MenuItem>
            <MenuItem value={"poolSizeHL"}>Pool Size(High-Low)</MenuItem>
            <MenuItem value={"poolSizeLH"}>Pool Size(Low-High)</MenuItem>
          </Select>
        </FormControl>
      </Stack>
      <Stack>
        <Stack direction="row">
          <Grid
            container
            spacing={2}
            sx={{ paddingInline: "30px", mt: "10px" }}
          >
            <GridItem>
              <Typography>Tank</Typography>
            </GridItem>
            <GridItem>
              <Typography>Owner</Typography>
            </GridItem>
            <GridItem>
              <Typography>SDFTL</Typography>
            </GridItem>
            <GridItem>
              <Typography>Staked/Capacity</Typography>
            </GridItem>
          </Grid>
          <Box sx={{ width: "20px" }}></Box>
        </Stack>
        <InfiniteScroll
          dataLength={tankItems.length}
          next={fetchMore}
          hasMore={hasMore}
          loader={<Typography>Loading...</Typography>}
        >
          {tankItems.map((tankItem: TankItem, index: any) => (
            <PoolItem
              item={tankItem}
              index={index}
              key={index}
              expanded={expanded}
              handleExpand={handleExpand}
            />
          ))}
        </InfiniteScroll>
      </Stack>
    </Box>
  );
};
export default PoolsPage;
